/**
 * Small utility for redirecting to external pages (like product landing pages)
 *
 * Mostly useful to encapsulate the hard-to-test logic of changing sites in unit tests
 *
 * 'react-router' does not handle asynchronous redirects to external sites, and window.location.href is challenging to test in unit tests
 */
const redirectExternal = (url) => {
    console.log('Sending to external site: ', url);
    window.location.href = url;
};

export default redirectExternal;
