import {
    ActionGroup,
    Button,
    DescriptionList,
    DescriptionListDescription,
    DescriptionListGroup,
    DescriptionListTerm,
    Form,
    PageSection,
    Spinner,
    Title,
} from '@patternfly/react-core';
import React from 'react';
import { useEffect, useState } from 'react';
import { Redirect } from 'react-router-dom';
import PartnerOrderDetails from '../order-details/partner-order-details';
import getStratosphereErrorPaths from '../stratosphere-error-paths/get-stratosphere-error-paths';
import { getDefaultTerms, TermsFormGroup } from './terms-form-group';
import { useChrome } from '@redhat-cloud-services/frontend-components/useChrome';
import redirectExternal from './redirect-external';

// fake for local testing by changing this import to "...from '../bff-service/bff-service-fake'"
import { getPartnerOrderDetailsAsync, linkPartnerOrderToAccountAsync } from '../bff-service/bff-service';

function ReviewLinkAccount({ partnerId, local, useToast }) {
    const [mergedResponse, setMergedResponse] = useState(null);
    const [redirect, setRedirect] = useState(null);

    // form contents
    const [hideTerms, setHideTerms] = useState(false);
    const [isAgree, setIsAgree] = useState(false);
    const [termsPdfId, setTermsPdfId] = useState(null);

    // concurrent request control
    const [submitting, setSubmitting] = useState(false);
    const [fetchingResponse, setFetchingResponse] = useState(false);

    // Red Hat frontend components
    const { analytics, auth } = useChrome();

    // for cancelling requests to the BFF
    const abortController = new AbortController();

    /*
     * Various helper functions
     */

    const errorPaths = () => {
        return getStratosphereErrorPaths(partnerId);
    };

    const initializeMergedResponse = (data) => {
        //cleaner to just set state here since we have to update isAgree to enable the connect button
        if (!data.terms || data.terms.length < 1) {
            setIsAgree(true);
            setHideTerms(true);
        } else {
            setTermsPdfId(getDefaultTerms(data.terms).id);
        }
        setMergedResponse(data);
    };

    const fetchMergedResponse = () => {
        if (mergedResponse === null && !fetchingResponse) {
            console.debug('Starting to fetch entitlement details.');

            setFetchingResponse(true);
            getPartnerOrderDetailsAsync(auth, partnerId, abortController.signal)
                .then((res) => {
                    console.debug('Fetched merged response: ', JSON.stringify(res.data));

                    // If it's a 200 response but the body says the request wasn't successful
                    // call the error callback and make it look like an axios error, for compatibility
                    if (typeof res?.data?.successful === 'boolean' && !res.data.successful) {
                        errorCallback({ response: res });
                    } else {
                        initializeMergedResponse(res.data);
                    }
                })
                .catch((error) => {
                    errorCallback(error);
                })
                .finally(() => setFetchingResponse(false));
        }
    };

    const linkAccounts = () => {
        if (!submitting) {
            setSubmitting(true);

            linkPartnerOrderToAccountAsync(auth, partnerId, termsPdfId)
                .then((res) => onLinkSuccess(res))
                .catch((err) => errorCallback(err))
                .finally(() => setSubmitting(false));
        }
    };

    const onLinkSuccess = (success) => {
        if (!success) {
            setRedirect(errorPaths().error());
        }

        // show success then redirect
        useToast({
            key: 'confirm-link',
            message: local(`${partnerId}:toast-notification.confirm-link.success`),
            type: 'success',
        });

        analytics.track('MarketplaceConnectEvent', {
            outcome: 'link-success',
            partner: partnerId,
            locale: local.ns,
            rhAccountNumber: mergedResponse.redHatAccountDetails.accountNumber,
            rhProductList: mergedResponse.vendorOfferings,
        });

        setRedirect(landingPage(mergedResponse));
    };

    const landingPage = (data) => {
        let vendorOfferings = data?.vendorOfferings;
        redirectExternal(vendorOfferings?.[0]?.landingPage || window['runConfig']['hrefConfig']);
    };

    const errorCallback = (error) => {
        console.debug('Encountered error on call: ', JSON.stringify(error));

        if (error.reason === 'AbortError' || error.message === 'canceled') {
            // do not redirect if aborted
            return;
        }

        if (error?.response?.status === 401) {
            setRedirect(errorPaths().unauthorized());
        }

        console.debug('Error reason: ', error?.response?.data?.reason);

        switch (error?.response?.data?.reason) {
            case 'terms_error':
                setRedirect(errorPaths().error());
                break;
            case 'export_compliance_failure':
                setRedirect(errorPaths().exportError());
                break;
            case 'linked_different_account': // fall-through
            case 'linked_same_account':
                setRedirect(errorPaths().orderError());
                break;
            case 'missing_seller':
            case 'missing_token':
            case 'invalid_token':
            case 'expired_token':
                setRedirect(errorPaths().tokenError());
                break;
            default:
                setRedirect(errorPaths().systemError());
        }
    };

    // useEffect initializes fetching the merged response object
    useEffect(() => {
        // start fetching when rendered
        console.debug('Starting to fetch order details');
        fetchMergedResponse();

        // return cleanup code
        return () => {
            console.debug('Cancelling ongoing requests...');
            abortController.abort();
        };
    }, [partnerId]); // only fetch again if partner ID changes

    /*
     * Start actually handling the render...
     */

    // if a redirect URL was set, redirect to that immediately
    if (redirect) {
        console.log('Redirecting to: ', redirect);
        const errorInfo = {
            outcome: 'error',
            partner: partnerId,
            errorType: redirect,
        };
        analytics.track('MarketplaceConnectEvent', errorInfo);
        return <Redirect to={redirect} />;
    }

    // check if merged response has been set yet. If not, show a spinner while waiting
    if (!mergedResponse) {
        return <Spinner size="xl" aria-label="Waiting on order content to load" />;
    }

    const { marketplaceDetails, redHatAccountDetails, vendorOfferings, terms, linkedRedHatAccountId } = mergedResponse;

    // handle scenarios where the entitlement is already linked
    if (linkedRedHatAccountId) {
        console.debug('Entitlement already linked to: ', linkedRedHatAccountId);

        // cannot return JSX from the "getUser(..)" result since it is asynchronous
        auth.getUser().then((user) => {

            console.debug('Current user ord_id: ', user.identity.org_id);

            // if already linked to the current user, just send them to the landing page
            if (user.identity.org_id === linkedRedHatAccountId) {
                setRedirect(landingPage(mergedResponse));
                return;
            }

            const errorInfo = {
                outcome: 'already-linked',
                partner: partnerId,
                errorType: redirect,
                rhAccountNumber: redHatAccountDetails.accountNumber,
                rhProductList: vendorOfferings,
            };
            analytics.track('MarketplaceConnectEvent', errorInfo);

            // assemble account details to include on the error page
            const accountDetails = {
                // only one of these partner identifiers should be populated
                awsAccountId: marketplaceDetails.customerAccountId,
                azureAccountId: marketplaceDetails.azureCustomerid,
                gcpAccountId: marketplaceDetails.gcpAccountId,
                // might be a personal or company identifier
                rhAccountNumber: redHatAccountDetails.accountNumber || redHatAccountDetails.company,
            };

            console.debug('Account details: ', accountDetails);

            const encodedData = encodeURIComponent(JSON.stringify(accountDetails));
            const redirectUrl = errorPaths().alreadyLinked() + `?data=${encodedData}`;
            setRedirect(redirectUrl);
        });
    }

    // finally,if not already linked, show the linking page
    return (
        <>
            <PageSection isFilled={true} variant="light" className="pf-u-p-2xl">
                {/* TODO update to use localization */}
                <Title headingLevel="h1" className="pf-u-pb-xl">
                    Complete your account connection
                </Title>
                <DescriptionList
                    isHorizontal
                    horizontalTermWidthModifier={{
                        default: '12ch',
                        sm: '17ch',
                        md: '17ch',
                        lg: '17ch',
                        xl: '17ch',
                        '2xl': '17ch',
                    }}
                    className="pf-u-pb-2xl"
                >
                    <DescriptionListGroup>
                        <DescriptionListTerm>{local('review-link-account.accountNumberLabel')}</DescriptionListTerm>
                        <DescriptionListDescription>
                            {redHatAccountDetails.accountNumber ? redHatAccountDetails.accountNumber : local('review-link-account.defaultRedHatAccountNumber')}
                        </DescriptionListDescription>
                    </DescriptionListGroup>
                    {new PartnerOrderDetails(partnerId, marketplaceDetails, local).getRows().map((row, i) => (
                        <DescriptionListGroup key={i}>
                            <DescriptionListTerm>{row.label}</DescriptionListTerm>
                            <DescriptionListDescription>{row.value}</DescriptionListDescription>
                        </DescriptionListGroup>
                    ))}
                    <DescriptionListGroup>
                        <DescriptionListTerm>{local('review-link-account.subscriptionLabel')}</DescriptionListTerm>
                        <DescriptionListDescription>{vendorOfferings?.[0]?.description}</DescriptionListDescription>
                    </DescriptionListGroup>
                </DescriptionList>
                <Form>
                    {
                        /* Embedded component for handling terms, need to remove when empty */
                        hideTerms || (
                            <TermsFormGroup local={local} terms={terms} onAgreeToTerms={(agree) => setIsAgree(agree)} onSelectTermsId={(id) => setTermsPdfId(id)} />
                        )
                    }
                    <ActionGroup>
                        <Button variant="primary" type="button" isDisabled={!isAgree || submitting} onClick={() => linkAccounts()}>
                            {local('review-link-account.connectAccountButtonLabel')}
                        </Button>
                        <Button variant="link" href="/" component="a" style={{ paddingTop: '6px' }}>
                            {local('review-link-account.cancelButtonLabel')}
                        </Button>
                    </ActionGroup>
                </Form>
            </PageSection>
        </>
    );
}

export default ReviewLinkAccount;
